.main-menu {

    &__block {
        padding-top: 35px;
    }

    ul {
        padding: 0;
        margin: 0;

        li {
            list-style: none;
            margin-bottom: 25px;

            a {
                color: #fff;
                text-decoration: none;
                transition: color 0.3s ease;

                &:hover {
                    color: rgba(250, 250, 250, 0.7);
                }
            }
        }
    }
}


@media (min-width: 992px) {
    .main-menu {
        margin-top: 0;
        margin-right: 15px;

        &__block {
            padding-top: 0;
        }

        &:last-child {
            margin-right: 0;
        }

        ul {
            li {
                display: inline-block;
                margin-right: 15px;

                &:last-child {
                    margin-right: 0;
                }
            }
        }   

        &__block {
            display: flex;
        }
    }
}

.main-info {
    p {
        &:last-child {
            margin-bottom: 0;
        }
    }
}


@media (min-width: 1200px) {
    .main-menu {
        margin-right: 125px;

        li {
            margin-right: 40px;
        }
    }
    
}
