body {
  font-size: 16px;
  text-transform: uppercase;
  color: #FFFFFF;
  background-color: #000;
  font-weight: normal;
  line-height: 1.3;
  font-family: "Arial";
  /*  background-image: url(../images/fon-mobile.png);
    background-position: center;
    background-size: cover;*/
}
html,
body {
  width: 100%;
  height: 100%;
  overflow: hidden;
}
* {
  box-sizing: border-box;
}
h1 {
  font-size: 16px;
  font-weight: normal;
  margin: 0;
  margin-bottom: 14px;
}
p {
  margin: 0;
  margin-bottom: 22px;
}
.wrapper {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.wrapper__body {
  display: flex;
  flex-direction: column;
  position: relative;
  z-index: 10;
  width: 100%;
  height: 100%;
  padding: 12px 8px;
  padding-right: 13%;
  padding-bottom: 3px;
  overflow-y: auto;
}
.wrapper__info {
  width: 100%;
  position: relative;
  z-index: 5;
}
.wrapper__contact {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  position: relative;
  z-index: 5;
  flex-grow: 1;
  width: 100%;
}
@media (min-width: 992px) {
  body {
    font-size: 20px;
  }
  h1 {
    font-size: 20px;
  }
  .wrapper__body {
    flex-direction: row;
    padding: 16px;
    justify-content: space-between;
  }
  .wrapper__info {
    width: 490px;
    padding-right: 15px;
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    justify-content: space-between;
    flex: none;
  }
  .wrapper__contact {
    width: 465px;
    padding-left: 15px;
    flex: none;
  }
}
@media (min-width: 1200px) {
  .wrapper__contact {
    width: 575px;
  }
}
/* Blocks */
.main-menu__block {
  padding-top: 35px;
}
.main-menu ul {
  padding: 0;
  margin: 0;
}
.main-menu ul li {
  list-style: none;
  margin-bottom: 25px;
}
.main-menu ul li a {
  color: #fff;
  text-decoration: none;
  transition: color 0.3s ease;
}
.main-menu ul li a:hover {
  color: rgba(250, 250, 250, 0.7);
}
@media (min-width: 992px) {
  .main-menu {
    margin-top: 0;
    margin-right: 15px;
  }
  .main-menu__block {
    padding-top: 0;
  }
  .main-menu:last-child {
    margin-right: 0;
  }
  .main-menu ul li {
    display: inline-block;
    margin-right: 15px;
  }
  .main-menu ul li:last-child {
    margin-right: 0;
  }
  .main-menu__block {
    display: flex;
  }
}
.main-info p:last-child {
  margin-bottom: 0;
}
@media (min-width: 1200px) {
  .main-menu {
    margin-right: 125px;
  }
  .main-menu li {
    margin-right: 40px;
  }
}
.contact__item {
  margin-bottom: 23px;
}
.contact__item:last-child {
  margin-bottom: 0;
}
.contact__title {
  margin-bottom: -3px;
}
.contact__data {
  text-transform: lowercase;
  color: #fff;
  text-decoration: none;
  display: inline-block;
  transition: color 0.3s ease;
}
.contact__data:hover {
  color: rgba(250, 250, 250, 0.7);
}
.video {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 1;
  width: 100%;
  height: 100%;
  display: block;
}
.video video {
  min-width: 100%;
  min-height: 100%;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  height: auto;
  width: auto;
  bottom: 0;
}
@media (max-width: 992px) {
  .video video {
    /* left: 0;
            transform: translateX(0);*/
  }
}
@media (max-width: 768px) {
  .video {
    width: 292%;
  }
}
@media (max-width: 560px) {
  .video {
    width: 200%;
    left: -100%;
    left: initial;
    right: 0;
    top: auto;
    bottom: 0;
  }
  .video video {
    /* left: 0;
            transform: translateX(0);*/
  }
}
@media (max-width: 560px) and (min-aspect-ratio: 16/9) {
  .video {
    height: 300%;
    top: -100%;
  }
}
