.video {
    position: absolute;
    left: 0;
    top: 0;
    z-index: 1;
    width: 100%;
    height: 100%;
    display: block;

    video {
        min-width: 100%;
        min-height: 100%;
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        height: auto;
        width: auto;
        bottom: 0;
    }
}

@media (max-width: 992px) {
    .video {

        video {
           /* left: 0;
            transform: translateX(0);*/
        }
	}
}

@media (max-width: 768px) {
	.video {
        width: 292%;
	}
}


@media (max-width: 560px) {
    .video {
        width: 200%;
        left: -100%;
        left: initial;
        right: 0;
        top: auto;
        bottom: 0;

        video {
           /* left: 0;
            transform: translateX(0);*/
        }
	}
}

@media (max-width: 560px) and (min-aspect-ratio: 16/9) {
	.video {
		height: 300%;
		top: -100%;
	}
}

