.contact {
    &__item {
        margin-bottom: 23px;

        &:last-child {
            margin-bottom: 0;
        }
    }

    &__title {
        margin-bottom: -3px;
    }

    &__data {
        text-transform: lowercase;
        color: #fff;
        text-decoration: none;
        display: inline-block;
        transition: color 0.3s ease;

        &:hover {
            color: rgba(250, 250, 250, 0.7);
        }
    }
}


    