body {
    //font-family: SuisseBPIntl;
    font-size: 16px;
    text-transform: uppercase;
    color: #FFFFFF;
    background-color: #000;
    font-weight: normal;
    line-height: 1.3;
    font-family: "Arial";
  /*  background-image: url(../images/fon-mobile.png);
    background-position: center;
    background-size: cover;*/
}

html, body {
    width: 100%;
    height: 100%;
    overflow: hidden;
}

* {
    box-sizing: border-box;
}

h1 {
    font-size: 16px;
    font-weight: normal;
    margin: 0;
    margin-bottom: 14px;
}

p {
    margin: 0;
    margin-bottom: 22px;
}

.wrapper {
    position: relative;
    width: 100%;
    height: 100%;
    overflow: hidden;

    &__body {
        display: flex;
        flex-direction: column;
        position: relative;
        z-index: 10;
        width: 100%;
        height: 100%;
        padding: 12px 8px;
        padding-right: 13%;
        padding-bottom: 3px;
        overflow-y: auto;
    
    }

    &__info {
        width: 100%;
        position: relative;
        z-index: 5;
    }

    &__contact {
        display: flex;
        justify-content: space-between;
        flex-direction: column;
        position: relative;
        z-index: 5;
        flex-grow: 1;
        width: 100%;
    }
}


@media (min-width: 992px) {
    body {
        font-size: 20px;
       // background-image: url("../images/fon.png");
    }

    h1 {
        font-size: 20px;
    }

    .wrapper {

        &__body {
            flex-direction: row;
            padding: 16px;
            justify-content: space-between;
        }

        &__info {
            width: 490px;
            padding-right: 15px;
            display: flex;
            flex-direction: column;
            flex-grow: 1;
            justify-content: space-between;
            flex: none;
        }

        &__contact {

            width: 465px;
            padding-left: 15px;
            flex: none;
        }
    }
}


@media (min-width: 1200px) {
    .wrapper {

        &__contact {
            width: 575px;
        }
    }
}